export default {
  init: function init() {
    var contactSubmenuToggle = function contactSubmenuToggle() {
      var contactSubmenuParents = document.querySelectorAll(".contact__submenu.level-one > li > button");
      contactSubmenuParents.forEach(function (parentItem) {
        parentItem.addEventListener("click", function (e) {
          if (e.target.classList.contains("open")) {
            e.target.classList.remove("open");
          } else {
            e.target.classList.add("open");
          }
        });
      });
    };

    contactSubmenuToggle();
  },
  finalize: function finalize() {// JavaScript to be fired on the home page, after the init JS
  }
};


