import Combobo from "combobo";
export default {
  init: function init() {
    // comboboxes
    var comboboEducation = new Combobo({
      input: "#education-level",
      list: ".listbox.education",
      options: ".option",
      // qualified within `list`
      openClass: "open",
      activeClass: "active",
      selectedClass: "selected",
      useLiveRegion: false,
      multiselect: false,
      noResultsText: null,
      selectionValue: function selectionValue(selecteds) {
        return selecteds.map(function (s) {
          return s.innerText.trim();
        }).join(" - ");
      },
      // eslint-disable-next-line max-len
      optionValue: "underline",
      // wrap the matched portion of the option (if applicable) in a span with class "underline"
      filter: "contains" // 'starts-with', 'equals', or funk

    });
    var comboboProgram = new Combobo({
      input: "#program",
      list: ".listbox.program",
      options: ".option",
      // qualified within `list`
      openClass: "open",
      activeClass: "active",
      selectedClass: "selected",
      useLiveRegion: false,
      multiselect: false,
      noResultsText: null,
      selectionValue: function selectionValue(selecteds) {
        return selecteds.map(function (s) {
          return s.innerText.trim();
        }).join(" - ");
      },
      // eslint-disable-next-line max-len
      optionValue: "underline",
      // wrap the matched portion of the option (if applicable) in a span with class "underline"
      filter: "contains" // 'starts-with', 'equals', or funk

    });

    var alternatingRows = function alternatingRows() {
      var programsVisible = $(".programs__grid__indiv:visible");
      var programsHidden = $(".programs__grid__indiv:hidden");
      programsHidden.removeClass("gray-bg");
      programsVisible.odd().addClass("gray-bg");
    };

    alternatingRows(); // clear all selections

    var clearAll = function clearAll() {
      var clearAllButton = document.querySelector("button#clear-all");
      clearAllButton.addEventListener("click", function () {
        // pass in all programs & show all
        var programs = document.querySelectorAll(".programs__grid__indiv");
        programs.forEach(function (program) {
          program.classList.remove("hidden");
          program.classList.remove("open");
          var clearPrograms = $(".programs__grid__indiv");
          clearPrograms.removeClass("gray-bg");
          clearPrograms.odd().addClass("gray-bg");
        }); // add open class back to first program

        var firstProgram = programs[0];
        firstProgram.classList.add("open"); // pass in search field & remove input

        var searchField = document.querySelector("input#program-search");
        searchField.value = "";
        comboboEducation.reset();
        comboboProgram.reset();
      });
    };

    clearAll(); // search majors

    var searchFunction = function searchFunction() {
      // pass in all programs
      var programs = document.querySelectorAll(".programs__grid__indiv");
      programs.forEach(function (program) {
        var programTitle = program.querySelector("h3.label-heading").innerText.toLowerCase();
        var programType = program.querySelector("span.type").innerText.toLowerCase();
        var programDept = program.querySelector("span.department").innerText.toLowerCase();
        var programDesc = program.querySelector("p.desc").innerText.toLowerCase(); // define search field

        var searchField = document.querySelector("input#program-search"); // capture user input

        searchField.addEventListener("input", function () {
          comboboEducation.reset();
          comboboProgram.reset();
          var rawUserInput = searchField.value;
          var userInput = rawUserInput.toLowerCase();
          program.classList.add("hidden");
          program.classList.remove("open");
          program.classList.remove("gray-bg"); // eslint-disable-next-line max-len

          if (programTitle.includes(userInput) || programType.includes(userInput) || programDept.includes(userInput) || programDesc.includes(userInput)) {
            program.classList.remove("hidden");
            alternatingRows();
          }
        });
      });
    };

    searchFunction();

    var filterByType = function filterByType() {
      // default array of choices - will be replaced when selections are made
      var currentSelectionArray = ["education selection", "program selection"];
      var currentSelectionEducation;
      var currentSelectionProgram;
      var educationActive = document.querySelector("input#education-level");
      var programActive = document.querySelector("input#program"); // make it work on tablet by linking buttons to dropdown choices

      if ($(".selections__buttons").is(":visible")) {
        // eslint-disable-next-line max-len
        var educationButtons = document.querySelectorAll(".selections__buttons > .education-level > button");
        var currentEducationLevel;
        educationButtons.forEach(function (educationButton, index) {
          educationButton.addEventListener("click", function (e) {
            educationButton.classList.remove("active"); // pass in search field & remove input

            var searchField = document.querySelector("input#program-search");
            searchField.value = "";
            educationButtons.forEach(function (eduBtn) {
              eduBtn.classList.remove("active");
            });
            comboboEducation.reset();
            currentEducationLevel = e.target.closest("button").classList[2].toLowerCase();
            comboboEducation.goTo(comboboEducation.getOptIndex() + index).select();
            e.target.closest("button").classList.add("active");
          });
        });
        var programButtons = document.querySelectorAll(".selections__buttons > .program-type > button");
        var currentProgramType;
        programButtons.forEach(function (programButton, index) {
          programButton.addEventListener("click", function (e) {
            programButton.classList.remove("active"); // pass in search field & remove input

            var searchField = document.querySelector("input#program-search");
            searchField.value = "";
            programButtons.forEach(function (prgrmBtn) {
              prgrmBtn.classList.remove("active");
            });
            comboboProgram.reset();
            currentProgramType = e.target.closest("button").classList[2].toLowerCase();
            comboboProgram.goTo(comboboProgram.getOptIndex() + index).select();
            e.target.closest("button").classList.add("active");
          });
        }); // remove active class if using  search

        var searchField = document.querySelector("input#program-search"); // capture user input

        searchField.addEventListener("input", function () {
          educationButtons.forEach(function (eduBtn) {
            eduBtn.classList.remove("active");
          });
          programButtons.forEach(function (prgrmBtn) {
            prgrmBtn.classList.remove("active");
          });
        }); //remove active class if using clear all

        var clearAllButton = document.querySelector("button#clear-all");
        clearAllButton.addEventListener("click", function () {
          educationButtons.forEach(function (eduBtn) {
            eduBtn.classList.remove("active");
          });
          programButtons.forEach(function (prgrmBtn) {
            prgrmBtn.classList.remove("active");
          });
        });
      } // pass in all programs


      var programs = document.querySelectorAll(".programs__grid__indiv");
      programs.forEach(function (program) {
        // listen for selection
        comboboEducation.addEventListener("selection", function (e) {
          // empty search field
          var searchField = document.querySelector("input#program-search");
          searchField.value = ""; // replace education with new selection

          program.classList.add("hidden");
          program.classList.remove("open");
          program.classList.remove("gray-bg");
          currentSelectionArray.shift();
          var educationCurrentSelection = e.option.classList[1];
          currentSelectionArray.unshift(educationCurrentSelection);
          currentSelectionEducation = currentSelectionArray[0]; // select button on desktop when you select on mobile=

          var educationBtns = document.querySelectorAll(".education-level > button");
          educationBtns.forEach(function (btn) {
            var btnClass = btn.classList[2];
            btn.classList.remove("active");

            if (btnClass == currentSelectionEducation) {
              btn.classList.add("active");
            }
          }); // eslint-disable-next-line max-len

          if (educationActive.hasAttribute("data-active-option") && programActive.hasAttribute("data-active-option")) {
            // eslint-disable-next-line max-len
            if (program.classList.contains(currentSelectionEducation) && program.classList.contains(currentSelectionProgram)) {
              program.classList.remove("hidden");
              alternatingRows();
            }
          } // eslint-disable-next-line max-len
          else if (educationActive.hasAttribute("data-active-option") && !programActive.hasAttribute("data-active-option")) {
            if (program.classList.contains(currentSelectionEducation)) {
              program.classList.remove("hidden");
              alternatingRows();
            }
          }
        });
        comboboProgram.addEventListener("selection", function (e) {
          // empty search field
          var searchField = document.querySelector("input#program-search");
          searchField.value = ""; // replace program with new selection

          program.classList.add("hidden");
          program.classList.remove("open");
          program.classList.remove("gray-bg");
          currentSelectionArray.pop();
          var programCurrentSelection = e.option.classList[1];
          currentSelectionArray.push(programCurrentSelection);
          currentSelectionProgram = currentSelectionArray[1]; // select button on desktop when you select on mobile=

          var programBtns = document.querySelectorAll(".program-type > button");
          programBtns.forEach(function (btn) {
            var btnClass = btn.classList[2];
            btn.classList.remove("active");

            if (btnClass == currentSelectionProgram) {
              btn.classList.add("active");
            }
          }); // eslint-disable-next-line max-len

          if (educationActive.hasAttribute("data-active-option") && programActive.hasAttribute("data-active-option")) {
            // eslint-disable-next-line max-len
            if (program.classList.contains(currentSelectionEducation) && program.classList.contains(currentSelectionProgram)) {
              program.classList.remove("hidden");
              alternatingRows();
            }
          } // eslint-disable-next-line max-len
          else if (programActive.hasAttribute("data-active-option") && !educationActive.hasAttribute("data-active-option")) {
            if (program.classList.contains(currentSelectionProgram)) {
              program.classList.remove("hidden");
              alternatingRows();
            }
          }
        });
      });
    };

    filterByType(); // program + major accordion for mobile

    var programAccordion = function programAccordion() {
      var programToggles = document.querySelectorAll("button.program-toggle");
      programToggles.forEach(function (toggle) {
        toggle.addEventListener("click", function (e) {
          var accordionItem = e.target.closest(".programs__grid__indiv");
          accordionItem.classList.toggle("open");
        });
      });
    };

    programAccordion();

    var stickyLetters = function stickyLetters() {
      $(window).on("load scroll", function () {
        var scrollYPos = $(document).scrollTop();
        var lettersOffset = $("#choose-letter").offset().top;

        if (scrollYPos > lettersOffset) {
          $(".alphabet").removeClass("fixed");
          $(".alphabet").addClass("sticky");
        } else {
          $(".alphabet").removeClass("sticky");
          $(".alphabet").addClass("fixed");
        }
      }); // smooth scroll anchor links & account for sticky header

      $("a[href*=\\#]").on("click", function (event) {
        event.preventDefault();

        if ($(".alphabet").hasClass("fixed")) {
          $("html,body").animate({
            scrollTop: $(this.hash).offset().top - 180
          }, 500);
        } else if ($(".alphabet").hasClass("sticky")) {
          $("html,body").animate({
            scrollTop: $(this.hash).offset().top - 70
          }, 500);
        }
      }); // $( ".js-scroll" ).click( function() {
      //   var headerHeight = 60;
      //   $( "html, body" ).animate( {
      //     scrollTop: $( $.attr( this, "href" ) ).offset().top - headerHeight
      //   }, 500 );
      //   return false;
      // } );
    };

    stickyLetters(); // alternate row colors dynamically
  },
  finalize: function finalize() {// JavaScript to be fired on the home page, after the init JS
  }
};


