import Combobo from "combobo";
import SimpleBar from "simplebar";
import MicroModal from "micromodal";
export default {
  init: function init() {
    MicroModal.init(); // media queries

    var desktopSm = window.matchMedia("(min-width: 992px)");
    var tablet = window.matchMedia("(min-width: 768px)");
    var mobile = window.matchMedia("(max-width: 767px)");
    $(window).on("load", function () {
      reorderEntryPreview();
    });
    $(window).on("resize", function () {
      setTimeout(function () {
        reorderEntryPreview();
      }, 200);
    }); // comboboxes

    var comboboCategories = new Combobo({
      input: "#directory-categories",
      list: ".listbox",
      options: ".option",
      // qualified within `list`
      openClass: "open",
      activeClass: "active",
      selectedClass: "selected",
      useLiveRegion: false,
      multiselect: false,
      noResultsText: null,
      selectionValue: function selectionValue(selecteds) {
        return selecteds.map(function (s) {
          return s.innerText.trim();
        }).join(" - ");
      },
      // eslint-disable-next-line max-len
      optionValue: "underline",
      // wrap the matched portion of the option (if applicable) in a span with class "underline"
      filter: "contains" // 'starts-with', 'equals', or funk

    });

    var alternatingRows = function alternatingRows() {
      var directoryVisible = $(".directory__grid__indiv:visible");
      var directoryHidden = $(".directory__grid__indiv:hidden");
      directoryHidden.removeClass("gray-bg");
      directoryVisible.even().addClass("gray-bg");
    };

    alternatingRows(); // clear all selections

    var clearAll = function clearAll() {
      var clearAllButton = document.querySelector("button#clear-all");
      clearAllButton.addEventListener("click", function () {
        // pass in all directory & show all
        var directory = document.querySelectorAll(".directory__grid__indiv");
        directory.forEach(function (entry) {
          entry.classList.remove("hidden");
          entry.classList.remove("open");
          var clearDirectory = $(".directory__grid__indiv");
          clearDirectory.removeClass("gray-bg");
          clearDirectory.even().addClass("gray-bg");
        }); // add open class back to first program

        var firstEntry = directory[0];
        firstEntry.classList.add("open"); // pass in search field & remove input

        var searchField = document.querySelector("input#directory-search");
        searchField.value = "";
        comboboCategories.reset();
      });
    };

    clearAll(); // search majors

    var searchFunction = function searchFunction() {
      // pass in all directory
      var directory = document.querySelectorAll(".directory__grid__indiv");
      directory.forEach(function (entry) {
        var entryDept = entry.querySelector(".department").innerText.toLowerCase();
        var entryEmail = entry.querySelector(".contact-info > .email > .copy").innerText.toLowerCase();
        var entryPhone = entry.querySelector(".contact-info > .phone > .copy").innerText.toLowerCase();
        var entryAddress = entry.querySelector(".contact-info > .address > .copy").innerText.toLowerCase();
        var entryPoBox = entry.querySelector(".contact-info > .po-box > .copy").innerText.toLowerCase();
        var entryHours = entry.querySelector(".contact-info > .hours > .copy").innerText.toLowerCase(); // define search field

        var searchField = document.querySelector("input#directory-search"); // capture user input

        searchField.addEventListener("input", function () {
          comboboCategories.reset();
          var rawUserInput = searchField.value;
          var userInput = rawUserInput.toLowerCase();
          entry.classList.add("hidden");
          entry.classList.remove("open");
          entry.classList.remove("gray-bg"); // eslint-disable-next-line max-len

          if (entryDept.includes(userInput) || entryEmail.includes(userInput) || entryPhone.includes(userInput) || entryAddress.includes(userInput) || entryPoBox.includes(userInput) || entryHours.includes(userInput)) {
            entry.classList.remove("hidden");
            alternatingRows();
          }
        });
      });
    };

    searchFunction();

    var filterByType = function filterByType() {
      var currentSelection;
      var categoryActive = document.querySelector("input#directory-categories"); // make it work on tablet by linking buttons to dropdown choices

      if ($(".selections__buttons").is(":visible")) {
        // eslint-disable-next-line max-len
        var categoryButtons = document.querySelectorAll(".selections__buttons > .directory-categories > button");
        categoryButtons.forEach(function (categoryButton, index) {
          categoryButton.addEventListener("click", function (e) {
            categoryButton.classList.remove("active"); // pass in search field & remove input

            var searchField = document.querySelector("input#directory-search");
            searchField.value = "";
            categoryButtons.forEach(function (catBtn) {
              catBtn.classList.remove("active");
            });
            comboboCategories.reset();
            currentSelection = e.target.closest("button").classList[2].toLowerCase();
            comboboCategories.goTo(comboboCategories.getOptIndex() + index).select();
            e.target.closest("button").classList.add("active");
          });
        }); // remove active class if using  search

        var searchField = document.querySelector("input#directory-search");
        searchField.addEventListener("input", function () {
          categoryButtons.forEach(function (dirBtn) {
            dirBtn.classList.remove("active");
          });
        }); //remove active class if using clear all

        var clearAllButton = document.querySelector("button#clear-all");
        clearAllButton.addEventListener("click", function () {
          categoryButtons.forEach(function (dirBtn) {
            dirBtn.classList.remove("active");
          });
        });
      } // pass in all directory entries


      var dirEntries = document.querySelectorAll(".directory__grid__indiv");
      dirEntries.forEach(function (entry) {
        // listen for selection
        comboboCategories.addEventListener("selection", function (e) {
          // empty search field
          var searchField = document.querySelector("input#directory-search");
          searchField.value = ""; // replace education with new selection

          entry.classList.add("hidden");
          entry.classList.remove("open");
          entry.classList.remove("gray-bg");
          currentSelection = e.option.classList[1]; // select button on desktop when you select on mobile=
          // eslint-disable-next-line max-len

          var categoryButtons = document.querySelectorAll(".selections__buttons > .directory-categories > button");
          categoryButtons.forEach(function (categBtn) {
            var btnClass = categBtn.classList[2];
            categBtn.classList.remove("active");

            if (btnClass == currentSelection) {
              categBtn.classList.add("active");
            }
          }); // eslint-disable-next-line max-len

          if (categoryActive.hasAttribute("data-active-option")) {
            // eslint-disable-next-line max-len
            if (entry.classList.contains(currentSelection)) {
              entry.classList.remove("hidden");
              alternatingRows();
            }
          }
        });
      });
    };

    filterByType();

    var stickyLetters = function stickyLetters() {
      $(window).on("load scroll", function () {
        var scrollYPos = $(document).scrollTop();
        var lettersOffset = $("#choose-letter").offset().top;

        if (scrollYPos > lettersOffset) {
          $(".alphabet").removeClass("fixed");
          $(".alphabet").addClass("sticky");
        } else {
          $(".alphabet").removeClass("sticky");
          $(".alphabet").addClass("fixed");
        }
      }); // smooth scroll anchor links & account for sticky header

      $("a[href*=\\#]").on("click", function (event) {
        event.preventDefault();

        if ($(".alphabet").hasClass("fixed")) {
          $("html,body").animate({
            scrollTop: $(this.hash).offset().top - 180
          }, 500);
        } else if ($(".alphabet").hasClass("sticky")) {
          $("html,body").animate({
            scrollTop: $(this.hash).offset().top - 70
          }, 500);
        }
      }); // $( ".js-scroll" ).click( function() {
      //   var headerHeight = 60;
      //   $( "html, body" ).animate( {
      //     scrollTop: $( $.attr( this, "href" ) ).offset().top - headerHeight
      //   }, 500 );
      //   return false;
      // } );
    };

    stickyLetters(); // reorder content on mobile / desktop for a11y

    var reorderEntryPreview = function reorderEntryPreview() {
      // faculty / staff
      var entryPreviews = document.querySelectorAll(".directory__grid__indiv.profile");
      entryPreviews.forEach(function (entryPreview) {
        var leftInfo = entryPreview.querySelector(".left-info");
        var entryMainInfo = entryPreview.querySelector(".main-info");
        var entryCta = entryPreview.querySelector(".modal-trigger");
        var entryPhoto = entryPreview.querySelector(".photo");
        var entryContact = entryPreview.querySelector(".contact-info");

        if (tablet.matches) {
          leftInfo.parentNode.insertBefore(entryContact, leftInfo.nextSibling);
          entryContact.parentNode.insertBefore(entryPhoto, entryContact.nextSibling);
          entryCta.parentNode.insertBefore(entryMainInfo, entryCta);
        } else {
          leftInfo.parentNode.insertBefore(entryPhoto, leftInfo);
          entryCta.parentNode.insertBefore(entryContact, entryCta);
        }
      }); // departments

      var deptPreviews = document.querySelectorAll(".directory__grid__indiv.department-listing");
      deptPreviews.forEach(function (deptPreview) {
        var deptLeftInfo = deptPreview.querySelector(".left-info");
        var deptMainInfo = deptPreview.querySelector(".main-info");
        var deptCta = deptPreview.querySelector(".cta");
        var deptPhoto = deptPreview.querySelector(".photo");
        var deptContact = deptPreview.querySelector(".contact-info");

        if (tablet.matches) {
          deptLeftInfo.parentNode.insertBefore(deptContact, deptLeftInfo.nextSibling);
          deptContact.parentNode.insertBefore(deptPhoto, deptContact.nextSibling);
          deptCta.parentNode.insertBefore(deptMainInfo, deptCta);
        } else {
          deptLeftInfo.parentNode.insertBefore(deptPhoto, deptLeftInfo);
          deptCta.parentNode.insertBefore(deptContact, deptCta);
        }
      });
    }; // let modalScroll = () => {
    //   // // only scroll bio scroll on bigger screens
    //   // let bioScroll = document.querySelector( ".entry-modal .bio .bio-scroll" );
    //   // let bioCustomScroll = new SimpleBar( bioScroll, { autoHide: false } );
    //   // // scroll whole modal on smaller screens
    //   // let modalScroll = document.querySelector( ".entry-modal .content .wrapper" );
    //   // let modalCustoMScroll = new SimpleBar( modalScroll, { autoHide: false } );
    //   let wrappers = document.querySelectorAll( ".entry-modal .content .wrapper" );
    //   wrappers.forEach( wrapper=> {
    //     if ( desktopSm.matches ) {
    //       console.log( "desktop-sm" );
    //       if ( wrapper.getAttribute( "data-simplebar" ) ) {
    //         // only scroll bio scroll on bigger screens
    //         wrapper.removeAttribute( "data-simplebar" )
    //         let bioScroll = document.querySelector( ".entry-modal .bio .bio-scroll" );
    //         let bioCustomScroll = new SimpleBar( bioScroll, { autoHide: false } );
    //         modalCustomScroll.unMount();
    //       }
    //       else {
    //         // only scroll bio scroll on bigger screens
    //         let bioScroll = document.querySelector( ".entry-modal .bio .bio-scroll" );
    //         let bioCustomScroll = new SimpleBar( bioScroll, { autoHide: false } );
    //       }
    //     }
    //     else {
    //       console.log( "nice" );
    //       let isBioScroll = wrapper.querySelector( ".bio-scroll" );
    //       if ( isBioScroll.getAttribute( "data-simplebar" ) ) {
    //         // scroll whole modal on smaller screens
    //         isBioScroll.removeAttribute( "data-simplebar" )
    //         let modalScroll = document.querySelector( ".entry-modal .content .wrapper" );
    //         let modalCustomScroll = new SimpleBar( modalScroll, { autoHide: false } );
    //         bioCustomScroll.unMount();
    //       }
    //       else {
    //         let modalScroll = document.querySelector( ".entry-modal .content .wrapper" );
    //         let modalCustomScroll = new SimpleBar( modalScroll, { autoHide: false } );
    //       }
    //     }
    //   } );
    // };


    var showModal = function showModal() {
      var modalTriggers = document.querySelectorAll(".modal-trigger");
      modalTriggers.forEach(function (modalTrigger) {
        var modalBtn = modalTrigger.querySelector(".btn");
        modalBtn.addEventListener("click", function () {
          MicroModal.show("entry-modal");
          $("html").addClass("no-scroll");
        });
      }); // let deptModalTriggers = document.querySelectorAll( ".department-listing .contact-info button" );
      // deptModalTriggers.forEach((deptModalTrigger)=> {
      //   deptModalTrigger
      // })

      var dialogOverlays = document.querySelectorAll(".modal .dialog");
      dialogOverlays.forEach(function (overlay) {
        console.log(overlay);
        overlay.addEventListener("click", function () {
          if ($(".entry-modal").hasClass("is-open")) {
            MicroModal.close("entry-modal");
          } else if ($(".dept-modal").hasClass("is-open")) {
            MicroModal.close("dept-modal");
          }

          $("html").removeClass("no-scroll");
        }); // prevent clicking on actual modal to exit

        var actualContent = overlay.querySelector(".dialog .content .content-bg");
        actualContent.addEventListener("click", function (e) {
          e.stopPropagation();
        });
        var contentWrapper = overlay.querySelector(".dialog .content");
        contentWrapper.addEventListener("click", function (e) {
          e.stopPropagation();
        });
      }); // close with x button

      var modalCloseBtns = document.querySelectorAll(".directory__grid__indiv .modal-close");
      modalCloseBtns.forEach(function (btn) {
        btn.addEventListener("click", function () {
          console.log(e.currentTarget);

          if ($("entry-modal").hasClass("is-open")) {
            MicroModal.close("entry-modal");
          } else if ($(".dept-modal").hasClass("is-open")) {
            MicroModal.close("dept-modal");
          }
        });
      }); // close on actual overlay click 

      var overlays = document.querySelectorAll(".modal .overlay");
      overlays.forEach(function (overlay) {
        console.log(overlay);
        overlay.addEventListener("click", function () {
          if ($(".entry-modal").hasClass("is-open")) {
            MicroModal.close("entry-modal");
          } else if ($(".dept-modal").hasClass("is-open")) {
            MicroModal.close("dept-modal");
          }

          $("html").removeClass("no-scroll");
        });
      }); // close on escape

      $(document).keydown(function (e) {
        if (e.key === "Escape") {
          // escape key maps to keycode `27`
          $("html").removeClass("no-scroll");
        }
      });
    };

    showModal(); // alternate row colors dynamically
  },
  finalize: function finalize() {// JavaScript to be fired on the home page, after the init JS
  }
};


