import Swiper, { Navigation, Pagination } from "swiper";
export default {
  init: function init() {
    // media queries
    var desktop = window.matchMedia("(min-width: 1200px)");
    var tablet = window.matchMedia("(min-width: 768px)");
    var mobile = window.matchMedia("(max-width: 767px)");
    $(window).on("load", function () {
      reorderStudentOrgs();
    });
    $(window).on("resize", function () {
      setTimeout(function () {
        reorderStudentOrgs();
      }, 200);
    });
    var aboutMcm = new Swiper(".about-campus-slider .swiper-container", {
      // Optional parameters
      direction: "horizontal",
      loop: false,
      // Navigation arrows
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
      }
    });
    var studentOrgs = new Swiper(".student-org-slider .swiper-container", {
      // Optional parameters
      direction: "horizontal",
      loop: false,
      // Navigation arrows
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
      }
    });

    var athleticsToggleSports = function athleticsToggleSports() {
      var athleticsToggle = $(".athletics__toggle > button");
      athleticsToggle.each(function () {
        $(this).on("click", function (e) {
          athleticsToggle.removeClass("active");
          e.target.classList.add("active");
          var currentSelection = e.target.classList[1];

          if (e.target.classList.contains("mens")) {
            $(".athletics__side > .mens__photos").removeClass("hidden");
            $(".athletics__content.womens").addClass("hidden");
            $(".athletics__side > .womens__photos").addClass("hidden");
            var sportsListsMen = $(".athletics__content.mens");
            sportsListsMen.each(function (index, list) {
              list.classList.add("hidden");
              var listClass = list.classList[1];

              if (listClass == currentSelection) {
                list.classList.remove("hidden");
              }
            });
          } else if (e.target.classList.contains("womens")) {
            $(".athletics__side > .womens__photos").removeClass("hidden");
            $(".athletics__content.mens").addClass("hidden");
            $(".athletics__side > .mens__photos").addClass("hidden");
            var sportsListsWomen = $(".athletics__content.womens");
            sportsListsWomen.each(function (index, list) {
              list.classList.add("hidden");
              var listClass = list.classList[1];

              if (listClass == currentSelection) {
                list.classList.remove("hidden");
              }
            });
          }
        });
      });
    };

    athleticsToggleSports();

    var athleticsHovers = function athleticsHovers() {
      var athleticsMensImgs = $(".athletics__side > .mens__photos > div");
      var sportHoverMen = $(".athletics__content.mens > ul > li");
      var hoveredMenSportClass;
      sportHoverMen.on("mouseenter", function (e) {
        // set the currently hovered sport to the hover
        sportHoverMen.removeClass("active");
        hoveredMenSportClass = e.target.closest("li").classList[0];
        var hoveredSport = e.target.closest("li");
        hoveredSport.classList.add("active");
        athleticsMensImgs.each(function (index, group) {
          group.classList.add("hidden");
          group.classList.remove("active");
          var sportPhotoGroup = group.classList[0];

          if (hoveredMenSportClass == sportPhotoGroup) {
            group.classList.remove("hidden");
          }
        });
      });
      var athleticsWomensImgs = $(".athletics__side > .womens__photos > div");
      var sportHoverWomen = $(".athletics__content.womens > ul > li");
      var hoveredWomenSportClass;
      sportHoverWomen.on("mouseenter", function (e) {
        // set the currently hovered sport to the hover
        sportHoverWomen.removeClass("active");
        hoveredWomenSportClass = e.target.closest("li").classList[0];
        var hoveredSport = e.target.closest("li");
        hoveredSport.classList.add("active");
        athleticsWomensImgs.each(function (index, group) {
          group.classList.add("hidden");
          group.classList.remove("active");
          var sportPhotoGroup = group.classList[0];

          if (hoveredWomenSportClass == sportPhotoGroup) {
            group.classList.remove("hidden");
          }
        });
      });
    };

    athleticsHovers();

    var reorderStudentOrgs = function reorderStudentOrgs() {
      var swiperSlides = document.querySelectorAll(".student-org-slider .swiper-slide");
      swiperSlides.forEach(function (slide) {
        var studentOrgHeadline = slide.querySelector(".content-left-middle");
        var leftTop = slide.querySelector(".content-left-top");
        var leftBottom = slide.querySelector(".content-left-bottom");
        var rightTop = slide.querySelector(".content-right-top");
        var rightMiddle = slide.querySelector(".content-right-middle");
        var rightBottom = slide.querySelector(".content-right-bottom");

        if (desktop.matches) {
          studentOrgHeadline.parentNode.insertBefore(leftTop, studentOrgHeadline);
          rightBottom.parentNode.insertBefore(rightMiddle, rightBottom);
          rightMiddle.parentNode.insertBefore(rightTop, rightMiddle);
        } else {
          leftTop.parentNode.insertBefore(studentOrgHeadline, leftTop);
          leftBottom.parentNode.insertBefore(rightMiddle, leftBottom);
          rightMiddle.parentNode.insertBefore(rightTop, rightMiddle);
        }
      });
    };
  },
  finalize: function finalize() {// JavaScript to be fired on the home page, after the init JS
  }
};


