import Combobo from "combobo";
export default {
  init: function init() {
    var comboboSelectRoleTuition = new Combobo({
      input: "#select-role-tuition",
      list: ".listbox.select-role",
      options: ".option",
      // qualified within `list`
      openClass: "open",
      activeClass: "active",
      selectedClass: "selected",
      useLiveRegion: false,
      multiselect: false,
      noResultsText: null,
      selectionValue: function selectionValue(selecteds) {
        return selecteds.map(function (s) {
          return s.innerText.trim();
        }).join(" - ");
      },
      // eslint-disable-next-line max-len
      optionValue: "underline",
      // wrap the matched portion of the option (if applicable) in a span with class "underline"
      filter: "contains" // 'starts-with', 'equals', or funk

    });
    $(window).on("load", function () {
      comboboSelectRoleTuition.goTo(comboboSelectRoleTuition.getOptIndex() + 0).select();
      switchFinAidCta();
      reorderPieCharts();
    });
    $(window).on("resize", function () {
      // select correct button on window resize
      var currentlySelected = comboboSelectRoleTuition.currentOption.classList[1];

      if ($(".selection__buttons").is(":visible")) {
        var roleButtons = document.querySelectorAll(".selection__buttons > button");
        roleButtons.forEach(function (roleButton) {
          roleButton.classList.remove("active");
          var roleMatching = roleButton.classList[1];

          if (roleMatching == currentlySelected) {
            roleButton.classList.add("active");
          }
        });
      }

      switchFinAidCta();
      setTimeout(function () {
        reorderPieCharts();
      }, 200);
    }); // make it work on desktop by linking buttons to dropdown choices

    if ($(".selection__buttons").is(":visible")) {
      var roleButtons = document.querySelectorAll(".selection__buttons > button");
      roleButtons.forEach(function (roleButton, index) {
        roleButton.addEventListener("click", function (e) {
          roleButtons.forEach(function (roleBtn) {
            roleBtn.classList.remove("active");
          });
          comboboSelectRoleTuition.reset();
          comboboSelectRoleTuition.goTo(comboboSelectRoleTuition.getOptIndex() + index).select();
          e.target.classList.add("active");
        });
      });
    }

    var showSelectedRole = function showSelectedRole() {
      var currentRoleSelection;
      comboboSelectRoleTuition.addEventListener("selection", function (e) {
        // update the current selection variable
        currentRoleSelection = e.option.classList[1];
        var roleContentContainer = document.querySelectorAll(".select-role__content__indiv");
        roleContentContainer.forEach(function (roleContainer) {
          // hide everything at first
          roleContainer.classList.add("hidden"); // if current role selection matches role container id unhide

          if (currentRoleSelection == roleContainer.id) {
            roleContainer.classList.remove("hidden");
          }
        });
      });
    };

    showSelectedRole(); // move cta text on desktop

    var switchFinAidCta = function switchFinAidCta() {
      var finaidLeft = $(".cta_module.finaid .left");
      var finaidRight = $(".cta_module.finaid .right");
      var finaidText = $(".cta_module.finaid .copy");

      if ($(".desktop-nav").is(":visible")) {
        finaidText.appendTo(finaidLeft);
      } else {
        finaidText.prependTo(finaidRight);
      }
    };

    var reorderPieCharts = function reorderPieCharts() {
      var pieCharts = $(".select-role__content .pie-chart");
      var tablet = window.matchMedia("(min-width: 768px)");
      pieCharts.each(function () {
        var subsection = $(this).parent();

        if (tablet.matches) {
          subsection.append($(this));
        } else {
          subsection.prepend($(this));
        }
      });
    };

    reorderPieCharts();
  },
  finalize: function finalize() {// JavaScript to be fired on the home page, after the init JS
  }
};


