import Combobo from "combobo";
export default {
  init: function init() {
    var comboboDate = new Combobo({
      input: "#filter-by-date",
      list: ".listbox.date",
      options: ".option",
      // qualified within `list`
      openClass: "open",
      activeClass: "active",
      selectedClass: "selected",
      useLiveRegion: false,
      multiselect: false,
      noResultsText: null,
      selectionValue: function selectionValue(selecteds) {
        return selecteds.map(function (s) {
          return s.innerText.trim();
        }).join(" - ");
      },
      // eslint-disable-next-line max-len
      optionValue: "underline",
      // wrap the matched portion of the option (if applicable) in a span with class "underline"
      filter: "contains" // 'starts-with', 'equals', or funk

    });
    var comboboSubject = new Combobo({
      input: "#filter-by-subject",
      list: ".listbox.subject",
      options: ".option",
      // qualified within `list`
      openClass: "open",
      activeClass: "active",
      selectedClass: "selected",
      useLiveRegion: false,
      multiselect: false,
      noResultsText: null,
      selectionValue: function selectionValue(selecteds) {
        return selecteds.map(function (s) {
          return s.innerText.trim();
        }).join(" - ");
      },
      // eslint-disable-next-line max-len
      optionValue: "underline",
      // wrap the matched portion of the option (if applicable) in a span with class "underline"
      filter: "contains" // 'starts-with', 'equals', or funk

    }); // clear all selections

    var clearAll = function clearAll() {
      var clearAllButton = document.querySelector("button#clear-all");
      clearAllButton.addEventListener("click", function () {
        // pass in search field & remove input
        var searchField = document.querySelector("input#filter-search");
        searchField.value = "";
        comboboDate.reset();
        comboboSubject.reset();
      });
    };

    clearAll(); // select publication accordion
    // program + major accordion for mobile

    var publicationAccordion = function publicationAccordion() {
      var pubToggles = document.querySelectorAll("button.pub-toggle");
      pubToggles.forEach(function (toggle) {
        toggle.addEventListener("click", function (e) {
          var allItems = document.querySelectorAll(".select-pub__grid__indiv");
          allItems.forEach(function (item) {
            item.classList.remove("open");
          });
          var accordionItem = e.target.closest(".select-pub__grid__indiv");
          accordionItem.classList.add("open");
        });
      });
    };

    publicationAccordion();
  },
  finalize: function finalize() {// JavaScript to be fired on the home page, after the init JS
  }
};


